.CardContainer {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  /* padding-top: 50px; */
  /* margin-bottom: 50px; */ }
  .CardContainer .loader {
    position: relative;
    top: 50%;
    left: 50%; }
  .CardContainer .card-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(590px, 1fr));
    position: absolute;
    top: 165px;
    bottom: 20px;
    left: 20px;
    right: 20px;
    overflow: scroll;
    padding: 10px 0 20px; }

.Card {
  background: #4D4D4D;
  color: white;
  margin: 10px auto;
  width: 580px;
  height: 150px;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 165px 66px; }
  .Card .card-text {
    padding: 10px;
    position: relative; }
    .Card .card-text a {
      font-family: 'Lobster', cursive;
      color: white;
      width: 100%;
      margin: 0;
      font-size: 30px;
      text-decoration: underline; }
    .Card .card-text p {
      color: #BFBFBF;
      font-size: 20px;
      margin: 10px 0 0; }
    .Card .card-text p:nth-child(2) {
      width: 325px; }
    .Card .card-text p:nth-child(3) {
      position: absolute;
      bottom: 10px;
      right: 0;
      font-size: 14px; }
  .Card .card-percentage {
    height: 100%;
    background: #BFBFBF;
    box-sizing: border-box;
    border: solid #4d4d4d 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    font-weight: bold;
    font-size: 18px; }
    .Card .card-percentage p {
      margin: 0; }
    .Card .card-percentage p:nth-child(2) {
      font-size: 55px; }
  .Card .card-favorite {
    height: 100%;
    background: #666666;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: 80px;
    font-weight: bold; }

.Header {
